import Scroll from 'base/scroll/scroll'
export default {
  props: {
    text: {
      type: String,
      default: ''
    },
    val: {
      type: String,
      default: ''
    },
    nextButton: {
      type: String,
      default: ''
    },
    backButton: {
      type: String,
      default: ''
    }

  },
  components: {
    Scroll
  },
  methods: {
    onNextClick(){
      this.$emit('afterNext')
    },
    onBackClick(){
      this.$emit('afterBack')
    }

  }
}



